/*=============malibu-desktop-uLoading===================*/
/*=============Begin-malibu-desktop-uLoading===================*/
.malibu-desktop-uLoading {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  background: #ffffff;
  z-index: 999999;
  /* display: none; */
  text-align: center;
  display: flex;
 
  font-family: Arial, Helvetica, sans-serif;
}

.malibu-desktop-uLoading-tree {
  position: absolute;
  object-fit: contain;
  z-index: 3;
}
.malibu-desktop-uLoading-music {
  position: absolute;
}
.malibu-desktop-uLoading-music.one {
  margin-top: 20px;
  margin-left: 320px;
}
.malibu-desktop-uLoading-music.two {
  margin-top: 15px;
  margin-left: 300px;
}
.malibu-desktop-uLoading-music.three {
  margin-top: 5px;
  margin-left: 280px;
}
.malibu-desktop-uLoading-music.four {
  margin-top: -10px;
  margin-left: 300px;
}
.malibu-desktop-uLoading-music.five {
  margin-top: -25px;
  margin-left: 260px;
}
.malibu-desktop-uLoading-music.six {
  margin-top: -50px;
  margin-left: 260px;
}
.malibu-desktop-uLoading-tree.one {
  margin-left: 270px;
  margin-top: 118px;
}
.malibu-desktop-uLoading-tree.two {
  margin-left: 305px;
  margin-top: 125px;
}
.malibu-desktop-uLoading-tree.three {
  margin-left: 340px;
  margin-top: 114px;
}
.malibu-desktop-uLoading-tree.four {
  margin-left: 465px;
  margin-top: 120px;
}
.malibu-desktop-uLoading-tree.five {
  margin-left: 490px;
  margin-top: 123px;
}
.malibu-desktop-uLoading-header {
  margin: auto;
}
.malibu-desktop-uLoading-progress-bar {
  width: 792px;
  height: 42px;
  position: relative;
  /* white */

  background: #ffffff;
  /* primary */
  padding-left: 4px;
  padding-right: 4px;
  border: 2px solid #5557e4;
  border-radius: 4px;
  margin-top: 230px;
}
.malibu-desktop-uLoading-progress {
  height: 30px;
  margin-top: 4px;

  /* primary */

  background: #5557e4;
  border-radius: 4px 0px 0px 4px;
}
.loading {
  position: absolute;
  width: 121px;
  height: 28px;

  font-weight: 500;
  font-size: 24px;
  line-height: 28px;

  /* primary */
  white-space: nowrap;
  color: #5557e4;
  margin-top: 19px;
  margin-left: -390px;
  text-align: left;
}
.number-percent {
  position: absolute;
  height: 28px;
  width: 89px;
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;

  /* primary */

  color: #5557e4;
  text-align: right;
  margin-top: 19px;
  margin-left: 305px;
}
.number-percent-input {
  outline: none;
  border: none;
  width: 57px;
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  color: #5557e4;
  text-align: right;
}
.malibu-desktop-uLoading-sun {
  background: #ff6584;
  width: 66px;
  height: 66px;
  border-radius: 50%;
  position: absolute;
  margin-left: 460px;
  margin-top: 90px;
}
.malibu-desktop-uLoading-ground {
  z-index: 0;
  margin-top: 135px;
}
.malibu-desktop-uLoading-chiken-eye {
  width: 11px;
  height: 11px;
  border-radius: 50%;
  background: #3f3d56;
  z-index: 4;
  position: absolute;
}
.malibu-desktop-uLoading-chiken-eye.left {
  margin-left: 33px;
  margin-top: -98px;
}
.malibu-desktop-uLoading-chiken-eye.right {
  margin-left: 75px;
  margin-top: -98px;
}
.malibu-desktop-uLoading-rocket {
  width: 46px;
  height: 46px;
  padding-top: 5px;
}
.malibu-desktop-uLoading-speed {
  margin-left: 26px;
}
.malibu-desktop-uLoading-speed label {
  color: rgba(73, 79, 89, 0.8);
  font-size: 16px;
  line-height: 19px;
  padding-right: 30px;
}
.malibu-desktop-uLoading-speed-number {
  padding-top: 10px;
  font-weight: bold;
  font-size: 20px;
  line-height: 23px;
  color: #6db54f;
  display: flex;
}
.malibu-desktop-uLoading-speed-number img {
  width: 28px;
  height: 28px;
  font-size: 28px;
  margin-left: 8px;
}
.malibu-desktop-uLoading-speed-number.error {
  color: #e9121d;
}
.malibu-desktop-uLoading-message-error {
  font-size: 16px;
  line-height: 19px;
  /* identical to box height */

  color: #e9121d;
  padding-top: 15px;
}
/*=============End-malibu-desktop-uLoading===================*/

