/*=============malibu-mobile-uLoading===================*/
/*=============Begin-malibu-mobile-uLoading===================*/
.malibu-mobile-uLoading {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  background: #ffffff;
  z-index: 999999;
  /* display: none; */
  text-align: center;
  display: flex;
 
  font-family: Arial, Helvetica, sans-serif;
}

.malibu-mobile-uLoading-tree {
  position: absolute;
  object-fit: contain;
  z-index: 3;
}
.malibu-mobile-uLoading-music {
  position: absolute;
}
.malibu-mobile-uLoading-music.one {
  margin-top: 55px;
  margin-left: calc(50% + 65px);
}
.malibu-mobile-uLoading-music.two {
  margin-top: 50px;
  margin-left: calc(50% + 45px);
}
.malibu-mobile-uLoading-music.three {
  margin-top: 40px;
  margin-left: calc(50% + 27px);
}
.malibu-mobile-uLoading-music.four {
  margin-top: 20px;
  margin-left: calc(50% + 40px);
}
.malibu-mobile-uLoading-music.five {
  margin-top: 10px;
  margin-left: calc(50% + 5px);
}
.malibu-mobile-uLoading-music.six {
  margin-top: -20px;
  margin-left: calc(50% - 5px);
}
.malibu-mobile-uLoading-tree.one {
  margin-left: calc(50% - 50px - 105px);
  margin-top: 118px;
}
.malibu-mobile-uLoading-tree.two {
  margin-left: calc(50% - 50px - 72px);
  margin-top: 125px;
}
.malibu-mobile-uLoading-tree.three {
  margin-left: calc(50% - 50px - 45px);
  margin-top: 114px;
}
.malibu-mobile-uLoading-tree.four {
  margin-left: calc(100% - 50px - 115px);
  margin-top: 120px;
}
.malibu-mobile-uLoading-tree.five {
  margin-left: calc(100% - 50px - 85px);
  margin-top: 123px;
}
.malibu-mobile-uLoading-header {
  margin: auto;
}
.malibu-mobile-uLoading-progress-bar {
  width: 100%;
  height: 32px;
  position: relative;
  /* white */

  background: #ffffff;
  /* primary */
  padding-left: 2px;
  padding-right: 2px;
  border: 2px solid #5557e4;
  border-radius: 4px;
  margin-top: 200px;
}
.malibu-mobile-uLoading-progress {
  height: 24px;
  margin-top: 2px;

  /* primary */

  background: #5557e4;
  border-radius: 4px 0px 0px 4px;
}

.malibu-mobile-uLoading-sun {
  background: #ff6584;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  position: absolute;
  margin-left: calc((100% - 100px - 70px));
  margin-top: 115px;
}
.malibu-mobile-uLoading-ground {
  z-index: 0;
  margin-top: 135px;
  margin-left: calc((100% - 100px - 87px) / 2);
}
.malibu-mobile-uLoading-chiken-eye {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #3f3d56;
  z-index: 4;
  position: absolute;
}
.malibu-mobile-uLoading-chiken-eye.left {
  margin-left: 23px;
  margin-top: -77px;
}
.malibu-mobile-uLoading-chiken-eye.right {
  margin-left: 55px;
  margin-top: -77px;
}
.malibu-mobile-uLoading .loading {
  position: absolute;
  width: 121px;
  height: 28px;

  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-align: left;
  /* primary */
  white-space: nowrap;
  color: #5557e4;
  margin-top: 11px;
  left: 0;
  margin-left: unset;
}
.malibu-mobile-uLoading .number-percent {
  position: absolute;
  height: 28px;
  width: 89px;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;

  /* primary */

  color: #5557e4;
  text-align: right;
  margin-top: 11px;
  margin-left: unset;
  right: 0px;
}
.malibu-mobile-uLoading .number-percent-input {
  outline: none;
  border: none;
  width: 57px;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;

  color: #5557e4;
  text-align: right;
}
.malibu-mobile-uLoading-header {
  width: calc(100% - 100px);
}
.malibu-mobile-uLoading-chicken {
  margin-left: calc((100% - 100px - 102px) / 2);
  width: 102px;
  height: 112px;
  margin-top: 33px;
}
.malibu-mobile-uLoading-chicken img {
  width: 102px;
}
.malibu-mobile-uLoading-rocket {
  width: 40px;
  height: 40px;
  padding-top: 11px;
}
.malibu-mobile-uLoading-rocket img {
  width: 40px;
  height: 40px;
}
.malibu-mobile-uLoading-speed {
  margin-left: 11px;
}
.malibu-mobile-uLoading-speed label {
  color: rgba(73, 79, 89, 0.8);
  font-size: 14px;
  line-height: 16px;
  padding-right: 30px;
}
.malibu-mobile-uLoading-speed-number {
  padding-top: 7px;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #6db54f;
  display: flex;
}
.malibu-mobile-uLoading-speed-number img {
  width: 28px;
  height: 28px;
  font-size: 28px;
  margin-left: 8px;
}
.malibu-mobile-uLoading-speed-number.error {
  color: #e9121d;
}
.malibu-mobile-uLoading-message-error {
  font-size: 14px;
  line-height: 16px;
  /* identical to box height */

  color: #e9121d;
  padding-top: 15px;
}
.malibu-desktop-uLoading.fade-in,
.malibu-tablet-uLoading.fade-in,
.malibu-mobile-uLoading.fade-in {
  animation: fadout 200ms linear;
}
@keyframes fadout {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0);
  }
}
/*=============End-malibu-mobile-uLoading===================*/
